// 功能方法>>>引用:
import React, { Component } from 'react';
import classNames from 'classnames';
import { inject, observer } from 'mobx-react';
import { Carousel, WingBlank } from 'antd-mobile';

//  公用方法 配置项 接口 枚举>>>引用:
import { logger } from '../../utils/monitor';
import { RabbitPayBtn } from '../component';
import { ModalLogin, BaseModal } from '../../components';
import { ACTIVITY_PROCESS_STATUS } from '../../constants';

// 样式>>引用:
import styles from './eleven.less';

/**
 * 活动枚举: 这里双十一活动id是1
 */
const activityId = 1;

/**
 * 双十一活动页面: 未参加过活动
 */
@inject(({ elevenStore }) => {
  return {
    // 数据
    productId: elevenStore.state.productId, // 商品id
    acticityCarouselInfo: elevenStore.state.acticityCarouselInfo, // 轮播文字
    activityStatus: elevenStore.state.activityStatus, // 活动状态
    modal: elevenStore.modal,
    // 函数
    initData: elevenStore.initData, // 初始化数据
    getCarouselInfo: elevenStore.getCarouselInfo, // 获取轮播数据
    buyingBtn: elevenStore.buyingBtn, // 用户购买前置校验
    changeModalValue: elevenStore.changeModalValue // 修改弹窗key
  };
})
@observer
export default class NoParticipate extends Component {
  constructor(props) {
    super(props);
    this.props.getCarouselInfo();
    this.refByPayBtn = React.createRef();
    this.refByRushBtn = React.createRef();
  }

  componentDidMount() {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: '用户未购买双十一优惠定金浏览页面'
    });
    const { activityStatus } = this.props;
    // 只有活动进行中才有滚动事件
    if (activityStatus === ACTIVITY_PROCESS_STATUS.ACTIVITY_PROCESSING) {
      // 这里不能添加事件节流，必须时时执行，添加节流会延迟执行。
      window.addEventListener('scroll', this.watchHeadScroll, false);
    }
  }

  /**
   * 活动进行中: 页面滚动事件
   */
  watchHeadScroll = () => {
    const wrapper = this.refByRushBtn.current;
    const scrollTop =
      window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
    const num = wrapper && Math.floor(wrapper.offsetTop + wrapper.offsetHeight - scrollTop);
    const footer = document.getElementById('eleven-footer');
    if (num < 1) {
      if (footer.classList.contains('eleven-footer-fixed')) {
        return false;
      }
      footer.classList.add('eleven-footer-fixed');
    } else {
      if (footer.classList.contains('eleven-footer-fixed')) {
        footer.classList.remove('eleven-footer-fixed');
      }
    }
  };

  /**
   * 支付按钮的前置校验
   */
  handlePreCheck = () => {
    return this.props.buyingBtn();
  };

  /**
   * 弹窗render
   * login: 登陆 业务弹窗
   */
  renderModal = () => {
    const { modal } = this.props;
    switch (modal) {
      case 'login':
        return (
          <ModalLogin
            actId={activityId}
            actType="DEPOSIT"
            onOk={this.handleLoginCallback}
            onCancel={this.handleHideModal}
          />
        );
      case 'rules':
        return (
          <BaseModal
            header={
              <div className={styles['info-header']}>
                <div className={styles['header-bg']}></div>
              </div>
            }
            closable={true}
            onCancel={this.handleHideModal}
          >
            <div className={styles.rules}>
              <div className={styles.content}></div>
            </div>
          </BaseModal>
        );
      default:
        return null;
    }
  };

  /**
   * 登录/注册成功的回调
   */
  handleLoginCallback = () => {
    this.handleHideModal();
    // 执行子组件的支付方法
    this.refByPayBtn.current.handleClick();
  };

  /**
   * 关闭modal
   */
  handleHideModal = () => {
    this.props.changeModalValue('');
  };

  /**
   * 支付成功后回调
   */
  successReplace = (num) => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: '双十一优惠定金支付成功'
    });
    this.props.initData(activityId);
  };

  /**
   * 取消支付后回调
   */
  payOnCancel = () => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: '双十一优惠定金支付取消'
    });
    this.props.initData(activityId);
  };

  /**
   * 支付失败后回调
   */
  payOnFail = () => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: '双十一优惠定金支付异常'
    });
    this.props.initData(activityId);
  };

  /**
   * 头部轮播文字
   */
  renderCarouselInfo = () => {
    const { activityStatus, acticityCarouselInfo } = this.props;
    if (!acticityCarouselInfo.length) {
      return;
    }
    if (activityStatus === ACTIVITY_PROCESS_STATUS.ACTIVITY_PROCESSING) {
      return (
        <WingBlank className={styles.carousel}>
          <Carousel vertical dots={false} dragging={false} swiping={false} autoplay infinite>
            {acticityCarouselInfo.map((item, index) => {
              return (
                <div key={`eleven-${index}`} className={styles.carouselFont}>
                  {item}
                </div>
              );
            })}
          </Carousel>
        </WingBlank>
      );
    }
    return null;
  };

  /**
   * 根据活动状态渲染按钮
   */
  renderActivityState = () => {
    const { productId, activityStatus } = this.props;
    switch (activityStatus) {
      case ACTIVITY_PROCESS_STATUS.ACTIVITY_NOT_START:
        return <div className={classNames(styles.buyingBtn, styles.notStart)}></div>;
      case ACTIVITY_PROCESS_STATUS.ACTIVITY_PROCESSING:
        return (
          <>
            <RabbitPayBtn
              ref={this.refByPayBtn}
              productId={productId}
              preCheck={this.handlePreCheck}
              onSuccess={this.successReplace}
              onCancel={this.payOnCancel}
              onFail={this.payOnFail}
            >
              <div
                ref={this.refByRushBtn}
                className={classNames(styles.buyingBtn, styles.processing)}
              ></div>
            </RabbitPayBtn>
            <RabbitPayBtn
              productId={productId}
              preCheck={this.handlePreCheck}
              onSuccess={this.successReplace}
              onCancel={this.payOnCancel}
              onFail={this.payOnFail}
            >
              <div id="eleven-footer" className={styles.footer}>
                <p>双十一最高优惠</p>
                <p>1元预定六重豪礼，仅限1000个名额</p>
              </div>
            </RabbitPayBtn>
          </>
        );
      case ACTIVITY_PROCESS_STATUS.ACTIVITY_END:
        return <div className={classNames(styles.buyingBtn, styles.ending)}></div>;
      default:
        return null;
    }
  };

  /**
   * 活动规则点击
   */
  activityRules = () => {
    this.props.changeModalValue('rules');
  };

  render() {
    /**
     * 活动进行中页面底部有导航，给底部留出高度
     */
    const { activityStatus } = this.props;
    return (
      <div
        className={classNames(styles.noParticipate, [
          activityStatus === ACTIVITY_PROCESS_STATUS.ACTIVITY_PROCESSING ? styles.mBFooter : null
        ])}
      >
        <div className={styles.header}>
          {this.renderCarouselInfo()}
          <div className={styles.rule} onClick={this.activityRules}>
            活动规则
          </div>
        </div>
        {this.renderActivityState()}
        {this.renderModal()}
      </div>
    );
  }
}
