import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { tools } from '../../utils';
import * as styles from './video.less';
import playImgSrc from '../../public/images/bunny/video_play.png';

let count = 0;

// const defaultSrc = 'https://resource-staging.mmears.com/market/video/3CE3BBD9-B6DF-483E-9A2A-D14D2C935CDE_20190505160841634.MOV';

/**
 * 视频播放器, 兼容H5 IOS和安卓
 */
class VideoPlayer extends PureComponent {
  static propTypes = {
    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    className: PropTypes.string,
    preload: PropTypes.string,
    poster: PropTypes.string, // 封面图片
    src: PropTypes.string, // 视频资源地址
    onPlay: PropTypes.func, // 函数: 播放回调
    onPlaying: PropTypes.func, // 函数：播放中回调
    onPause: PropTypes.func, // 函数: 播放暂停
    onEnd: PropTypes.func // 函数: 播放结束回调
  };

  static defaultProps = {
    width: 300, // 视频宽高比为： 3:4
    height: 400, // IOS、android 高度自适应
    className: '',
    preload: 'metadata', // auto: 默认下载视频    metadata: 只提取视频元数据     none: 不会预加载视频
    poster: '',
    src: '',
    onPlay: () => {},
    onPlaying: () => {},
    onPause: () => {},
    onEnd: () => {}
  };

  constructor(props) {
    super(props);

    this.isIOS = tools.isIOS(); // 是否是iOS系统
    this.isAndroid = tools.isAndroid(); // 是否是Android系统
    this.status = ''; // 状态, reset 已重置, play 已播放, 空字符串/默认值 没有播放过
    this.state = {
      playImage: 'show',
      playId: `mmears-box-${count++}` // 用以Android弹窗显示时，刷新播放器
    };
  }

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {
    clearTimeout(this.playTimer);
  }

  player = null;

  playTimer = null;

  /**
   * 初始化视频播放器
   * currentTime, duration
   */
  init = () => {
    if (this.isIOS) {
      this.player.setAttribute('playsinline', true); // 禁止 iPhone Safari 视频自动全屏
      this.player.setAttribute('webkit-playsinline', true); // 禁止 iPhone Safari 视频自动全屏
    } else {
      this.player.setAttribute('x5-playsinline', true);
    }
  };

  /**
   * 安卓 点击播放按钮
   */
  handleImagePlay = () => {
    this.setState({
      playImage: false
    });
    this.player.play();
  };

  /**
   * 开始播放
   */
  handlePlay = () => {
    const { onPlay } = this.props;
    onPlay && onPlay();

    // console.log('handlePlay >> 设置status=play');
    this.status = 'play';
    // console.log('this.status', this.status);
  };

  /**
   * 视频播放中
   */
  handlePlaying = () => {
    const { onPlaying } = this.props;
    onPlaying && onPlaying();
  };

  /**
   * 暂停
   */
  handlePause = () => {
    const { onPause } = this.props;
    onPause && onPause();

    // console.log('播放暂停', this.player);
  };

  /**
   * 播放结束
   */
  handleEnded = () => {
    const { onEnd } = this.props;
    if (this.isAndroid) {
      // 先播放
      this.player.play();
      // 延迟关闭, 解决安卓全屏播放结束后的广告问题
      this.playTimer = setTimeout(() => {
        this.player.pause();
      }, 60);
    }

    // console.log('播放结束', this.player);

    onEnd && onEnd();
  };

  /**
   * 重置视频播放器（Android）
   */
  resetPlayer = () => {
    // IOS不会发生z-index问题, 所以直接return
    if (this.isIOS) return;

    // 安卓在视频播放后, 会发生层级问题, 所以需要重置播放器的key

    // 没有播放过  或者 已重置过, 直接返回
    if (!this.status || this.status === 'reset') return;

    // console.log('resetPlayer >> 设置status=reset');
    this.status = 'reset';
    // console.log('this.status', this.status);
    this.setState({
      playImage: 'show',
      playId: `mmears-box-${count++}`
    });
  };

  /**
   * IOS视频播放器
   * 使用原生播放按钮
   * @returns {*}
   */
  renderIOSContent = () => {
    const { width, src, poster, className, preload } = this.props;

    const videoBoxClassName = classNames(styles.videoBox, className);

    return (
      <div
        className={videoBoxClassName}
        ref={(ele) => {
          this.videoPlayer = ele;
        }}
      >
        <video
          ref={(player) => {
            this.player = player;
          }}
          width={width}
          controls={true}
          src={src}
          poster={poster}
          preload={preload}
          className={className}
          onPlay={this.handlePlay}
          onPause={this.handlePause}
          onPlaying={this.handlePlaying}
          onEnded={this.handleEnded}
        >
          当前浏览器不支持最新的video播放
        </video>
      </div>
    );
  };

  /**
   * 安卓视频播放器
   * 安卓使用自定义播放按钮
   * @returns {*}
   */
  renderAndroidContent = () => {
    const { playId, playImage } = this.state;

    const { width, src, poster, className, preload } = this.props;

    const videoBoxClassName = classNames(styles.videoBox, className);

    return (
      <div
        className={videoBoxClassName}
        ref={(ele) => {
          this.videoPlayer = ele;
        }}
      >
        {playImage === 'show' ? <img src={playImgSrc} onClick={this.handleImagePlay} /> : null}
        <video
          key={playId}
          ref={(player) => {
            this.player = player;
          }}
          controls={false}
          src={src}
          width={width}
          poster={poster}
          preload={preload}
          className={className}
          onPlay={this.handlePlay}
          onPause={this.handlePause}
          onPlaying={this.handlePlaying}
          onEnded={this.handleEnded}
        >
          当前浏览器不支持最新的video播放
        </video>
      </div>
    );
  };

  render() {
    if (this.isIOS) {
      return this.renderIOSContent();
    }

    return this.renderAndroidContent();
  }
}

export default VideoPlayer;
