// 功能方法>>>引用:
import React, { Component } from 'react';

// 公用方法 配置项 接口 枚举>>>引用:
import { message, myToast, wechatUtils } from '../../../utils';
import * as tools from '../../../utils/tools';
import { rabbitPageService } from '../../../service';
import { ORDER_ERROR_CODE } from '../../../constants';
import { wechatStatus } from '../../../utils/wechat-utils';
import { logger } from '../../../utils/monitor';

const THROTTLE_TIME = 1500;

interface IRabbitPayBtnProps {
  productId: string;
  preCheck: () => Promise<boolean>;
  onSuccess: (orderNo?: string) => void;
  onCancel: () => void;
  onFail: () => void;
}

export default class RabbitPayBtn extends Component<IRabbitPayBtnProps> {
  static defaultProps = {
    productId: '',
    preCheck: () => {
      return Promise.resolve(true);
    },
    onSuccess: () => {},
    onCancel: () => {},
    onFail: () => {}
  };

  constructor(props: IRabbitPayBtnProps) {
    super(props);

    // @ts-ignore
    this.handleClick = tools.throttle(this.handleClick, THROTTLE_TIME);
  }

  handleClick = async () => {
    const { preCheck, productId, onCancel, onFail, onSuccess } = this.props;

    message.showLoading('处理中');
    logger.info({
      message: `开始前置校验...`
    });
    if (preCheck) {
      const res = await preCheck();
      if (!res) {
        logger.info({
          message: `前置校验结果: ${!!res}`
        });
        message.closeLoading();
        return;
      }
    }

    // 微信JS-SDK 签名失败
    if (!wechatStatus.ready) {
      myToast.warning(`请您刷新页面重试`);
      return;
    }

    rabbitPageService
      .getRabbitOrderPay(productId)
      .then((wechatPayOptions) => {
        logger.info({
          message: `获取微信公众号支付信息成功, 开始唤起微信公众号支付`
        });
        message.closeLoading();

        // 调用JS-SDK 唤起微信公众号支付
        const orderNo = wechatPayOptions.orderNo;

        wechatUtils.chooseWXPay({
          nonceStr: wechatPayOptions.nonceStr,
          package: wechatPayOptions.package,
          paySign: wechatPayOptions.paySign,
          signType: wechatPayOptions.signType,
          timestamp: wechatPayOptions.timestamp,
          success: () => {
            logger.info({
              message: `微信公众号支付, 支付成功, 订单号${orderNo}`
            });
            onSuccess && onSuccess(orderNo);
          },
          cancel: () => {
            logger.info({
              message: `微信公众号支付, 取消支付, 订单号${orderNo}`
            });
            onCancel && onCancel();
          },
          fail: (error: any) => {
            const { msg } = error;
            myToast.warning(msg);
            logger.error({
              message: `微信公众号支付发生错误, 订单号${orderNo}, 错误信息: ${JSON.stringify(
                error
              )}`
            });
            onFail && onFail();
          }
        });
      })
      .catch((error) => {
        const { code, msg } = error;
        message.closeLoading();

        logger.error({
          message: `获取微信公众号支付信息发生异常, ${JSON.stringify(error)}`
        });

        switch (code) {
          case ORDER_ERROR_CODE.BIZ_ORDER_EXPIRYTIME_OVER:
            myToast.warning('亲！您的订单已过期');
            return;
          case ORDER_ERROR_CODE.BIZ_PARAM_ERR:
            myToast.warning('请求参数错误');
            return;
          case ORDER_ERROR_CODE.BIZ_ORDER_HTTP_RESPONSE_IS_NULL:
            myToast.warning('订单状态错误');
            return;
          case ORDER_ERROR_CODE.BIZ_PRODUCT_NOT_EXIST:
            myToast.warning('商品不存在');
            return;
          case ORDER_ERROR_CODE.BIZ_PAYMENT_NOT_EXIST:
            myToast.warning('支付方式不存在');
            return;
          case ORDER_ERROR_CODE.BIZ_ORDER_IS_NOT_SUPPORT_PAYMENT:
            myToast.warning('支付渠道不支持');
            return;
          default:
            myToast.warning(msg);
        }
      });
  };

  render() {
    const { children } = this.props;
    return <div onClick={this.handleClick}>{children}</div>;
  }
}
