import React from 'react';
import style from './style.less';
import share_img from '../../../public/images/group/share.png';


export default function ShareLayerModal(props) {
    const {onCancel} = props;
    return (
        <div className={style['share-container']} onClick={() => onCancel && onCancel()}>
            <img src={share_img}/>
        </div>
    );
}
