import React from 'react';
import PropTypes from 'prop-types';
import {TipsModal} from '../../../components';

import style from './style.less';
/**
 * 无法参加团购弹窗提示
 */
function NoJoinTipsModal(props) {
    const {tipsText, onOk = null} = props;
    return (
        <TipsModal
            visible={true}
            headText="温馨提示"
            okText={'我知道了'}
            onOk={() => onOk && onOk()}>
            <div className={style['tips-container']}>
                <div className={style['tips-text']}>
                    {tipsText}
                </div>
                <div>
                    <span className={style['tips-memo']}>
                        请仔细阅读活动规则。如有疑问，可拨打客服电话：400-626-1626
                    </span>
                </div>
            </div>
        </TipsModal>
    );
}

NoJoinTipsModal.propTypes = {
    modalKey: PropTypes.string,
    tipsText: PropTypes.string
};

NoJoinTipsModal.defaultProps = {
    modalKey: '',
    tipsText: '啊哦，你不能作为团员参加哦～'
};

export default NoJoinTipsModal;
