// 功能方法>>>引用:
import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import * as tools from '@mmears/mmears-tools';

//  公用方法 配置项 接口 枚举>>>引用:
import { shareConfig } from '../../config';
import { logger } from '../../utils/monitor';
import { AccountsType, ACTIVITY_BUYING_STATUS } from '../../constants';
import { DocumentTitle, WechatConfig, checkUserAuth } from '../../components';

// 组件 公用组件>>>引用:
import { ServiceError } from '../common';
import NoParticipate from './no-participate';
import HasParticipate from './has-participate';

// 分享默认参数
const defaultShareOptions = shareConfig.eleven;

/**
 * 活动枚举: 这里双十一活动id是1
 */
const activityId = 1;

/**
 * 双十一活动页面
 */
@inject(({ elevenStore }) => {
  return {
    // 数据
    buyFlag: elevenStore.state.buyFlag, // 用户购买商品状态
    userId: elevenStore.state.userId, // 分享用的userId
    serviceError: elevenStore.state.serviceError, // 接口异常
    errorMsg: elevenStore.state.errorMsg, // 接口异常信息
    // 函数
    initData: elevenStore.initData
  };
})
@observer
@checkUserAuth({
  account: AccountsType.MAIN,
  support: 'wechat'
}) // 微信授权--魔力耳朵公众号
export default class Eleven extends Component {
  constructor(props) {
    super(props);
    this.props.initData(activityId);
  }

  componentDidMount() {
    const originAddress = tools.getQueryString('origin');
    /**
     * fromAddress 用来区分页面来源
     * article: 公众号推文
     * menu: H5菜单栏
     * share: 分享链接
     * order: 订单详情双11抵扣提示
     */
    switch (originAddress) {
      case 'article':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: '公众号推文浏览双十一优惠定金页面'
        });
        return;
      case 'menu':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: 'H5菜单栏浏览双十一优惠定金页面'
        });
        return;
      case 'share':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: '分享链接浏览双十一优惠定金页面'
        });
        return;
      case 'order':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: '订单详情双11抵扣提示浏览双十一优惠定金页面'
        });
        return;
      case 'channel':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: '渠道链接浏览双十一优惠定金页面'
        });
        return;
      default:
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: '浏览双十一优惠定金页面'
        });
        return;
    }
  }

  /**
   * 根据购买商品状态展示
   */
  renderPageByBuy = () => {
    const { buyFlag } = this.props;
    switch (buyFlag) {
      case ACTIVITY_BUYING_STATUS.NO:
        return <NoParticipate />;
      case ACTIVITY_BUYING_STATUS.YES:
        return <HasParticipate />;
      default:
        return null;
    }
  };

  /**
   * 分享用到的参数
   * 这里用户如果登陆了, 分享rf渠道码有值的时候，分享时url携带参数rf=userId
   */
  shareParam = () => {
    const rfCode = Number(tools.getQueryString('rf')) !== -1 && tools.getQueryString('rf');
    const { userId } = this.props;
    const shareConfig = {
      ...defaultShareOptions,
      link:
        rfCode && userId
          ? `${window.location.protocol}//${window.location.host}${window.location.pathname}?origin=share&rf=${userId}`
          : `${window.location.protocol}//${window.location.host}${window.location.pathname}?origin=share&rf=${rfCode}`
    };
    return shareConfig;
  };

  render() {
    const { serviceError, errorMsg, userId } = this.props;
    // 获取信息时发生异常, 阻塞掉代码不往下走了
    if (serviceError) {
      return <ServiceError error={errorMsg} />;
    }

    const rfCode = Number(tools.getQueryString('rf')) !== -1 && tools.getQueryString('rf');
    const wechatConfigKey = rfCode + userId; // 设置key为了更新WechatConfig组件/或写2次WechatConfig组件也可以
    return (
      <>
        {this.renderPageByBuy()}
        <DocumentTitle title="魔力耳朵少儿英语" />
        <WechatConfig
          key={wechatConfigKey}
          showShare={true}
          shareOptions={this.shareParam()}
          account={AccountsType.MAIN}
        />
        {/* {rfCode && userId && (
          <WechatConfig
            showShare={true}
            shareOptions={this.shareParam()}
            account={AccountsType.MAIN}
          />
        )} */}
      </>
    );
  }
}
